import React, {useState, useEffect} from 'react';
import ReactDOM from 'react-dom';
import './index.css';

function GitHubUser({login}){

 const [data, setData] = useState(null);
 const [name, changeName] = useState("karan-ksrk");
 const [name1, changeName1] = useState("karan-ksrk");
 
 useEffect(()=>{
   fetch(`https://api.github.com/users/${name1}`)
   .then(res => res.json())
   .then(setData)
   .catch(console.error);
 }, [name1]);

 useEffect(()=>
  {
    console.log(`name: ${name1}`);
  },
  [name1] 
 );
if (data){
  return ( 
    <>
  <div>
    <center>
    <label>Enter Github username : </label>
    <input value={name}
     onChange = {e => changeName(e.target.value)}
     />
     <br/><br/>
     <button onClick={()=>changeName1(name)} >next</button>
     </center>
    </div>
  <div>
    <center> <h1>{data.login}</h1>
    <img src={data.avatar_url} width={200}></img></center>
  </div>
  </>
)

}
return null;

}



function App(){
  return (

    <GitHubUser />
  
  )
}

ReactDOM.render(
    <App />,
 
  document.getElementById('root')
);

